import React, {Component} from 'react';
import app from '../../base';

class ImageUpload extends Component {
    state = {
      image: '',
      url: '',
      progress: 0
    }
  componentDidMount(){
    this.getPhotoUrl();
  }
  handleChange = e => {
    if (e.target.files[0]) {
      const image = e.target.files[0];
      this.setState(() => ({image}));
    }
  }
  handleUpload = () => {
      const {image} = this.state;
      if(!image.length) return null;
      const uploadTask = app.storage().ref(`images/user_${app.auth().currentUser.uid}`).put(image);
      uploadTask.on('state_changed', 
      (snapshot) => {
        const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100);
        this.setState({progress});
      }, 
      (error) => {
        console.log(error);
      }, 
    () => {
        app.storage().ref('images').child(`user_${app.auth().currentUser.uid}`).getDownloadURL().then(url => {
            console.log(url);
            this.setState({url});
        })
    });
  }
  getPhotoUrl = () => {
    const currentUser = app.auth().currentUser;
    
    const photoRef = app.storage().ref('images').child(`user_${currentUser.uid}`);
    console.log(app.storage().ref(`images`).child);
    if(photoRef){
      photoRef.getDownloadURL().then(url => {
      
        console.log(url);
        this.setState({url});
      })
    }
    else if(currentUser.photoURL){
      this.setState({url: currentUser.photoURL});
      console.log(currentUser.photoURL);
    }
  }
  render() {
    return (
      <div>
        <input type="file" onChange={this.handleChange}/>
        <progress value={this.state.progress} max="100"/>
        <img src={this.state.url || 'http://via.placeholder.com/200x200'} alt="Uploaded images" height="200" width="200"/>
        <button onClick={this.handleUpload}>Upload</button>
      </div>
    )
  }
}

export default ImageUpload;